
.Chains{
    margin-top: 50px;
}
.paginationBttns{
  display: flex;
  list-style: none;
  width: 80%;
  height: 40px;
  justify-content: center;
}
.paginationBttns a{
    color: black;
    margin: 10px;
    padding: 15px;
    border-radius: 5px;
    border: 1px solid black;
    cursor: pointer;
}
.paginationBttns a:hover{
    background: black;
    color: white;
}
.paginationActive a{
    background-color: black;
    color: white;
    

}